<template>
  <section class="reading" v-if="read" @scroll="scroll" ref="read">
    <section :class="value.theme" class="content">
      <div class="cnt" v-for="(t, index) in title" :key="index">
        <div class="title" :style="{ fontSize: `${value.size + 4}px` }">{{ t }}</div>
        <div class="con">
          <p
            class="cons"
            :style="{ fontSize: `${value.size}px` }"
            v-for="(item, index) in opcon[index]"
            :key="index"
          >
            {{ item }}
          </p>
        </div>
      </div>

      <section class="mask" @click.stop="show = true">
        <transition
          name="custom-classes-transition"
          enter-active-class="animate__animated animate__slideInDown"
          leave-active-class="animate__animated animate__slideOutUp"
        >
          <div v-show="show" class="titles">
            <p @click="$router.go(-1)">返回</p>
            <p>{{ read.title }}</p>
            <p @click="icon = !icon">icon</p>
          </div>
        </transition>
        <div
          v-show="show"
          class="msk"
          @click.stop="
            show = false;
            icon = false;
          "
        >
          <ul v-if="icon">
            <li @click.stop="$router.push('/')">首页</li>
            <li @click.stop="$router.push('/mybooks')">书架</li>
            <li @click.stop="$router.push('/detail?id=' + bookid)">详情</li>
          </ul>
        </div>
        <transition
          name="custom-classes-transition"
          enter-active-class="animate__animated animate__slideInUp"
          leave-active-class="animate__animated animate__slideOutDown"
        >
          <div v-show="show" class="bottom">
            <div class="top">
              <div
                class="left"
                @click="
                  value.size--;
                  xxx();
                "
              >
                A-
              </div>
              <van-slider v-model="value.size" @change="onChange" :min="12" :max="24" :step="1" />
              <div
                class="right"
                @click="
                  value.size++;
                  xxx();
                "
              >
                A+
              </div>
            </div>
            <div class="con">
              <div
                v-for="item in themes"
                :key="item.text"
                @click="
                  value.theme = item.text;
                  xxx();
                "
                :style="{ backgroundColor: item.bg }"
              >
                <p>{{ item.name }}</p>
              </div>
            </div>
            <div class="bom">
              <div class="left" @click="PreviousChapter">上一章</div>
              <div class="conmuk">目录</div>
              <div class="right" @click="NextChapter">下一章</div>
            </div>
          </div>
        </transition>
      </section>
    </section>
  </section>
</template>

<script>
import { getChapterstwo } from "@/apis/index.js";
export default {
  components: {},
  data() {
    return {
      themes: [
        {
          name: "白天",
          text: "white",
          bg: "white",
        },
        {
          name: "黑夜",
          text: "dark",
          bg: "#333",
        },
        {
          name: "护眼",
          text: "huyan",
          bg: "#deeeda",
        },
        {
          name: "复古",
          text: "niupi",
          bg: "#dfd1c0",
        },
      ],
      value: JSON.parse(window.localStorage.getItem("modes")) || {
        size: 14,
        theme: "white",
      },
      read: null,
      show: false,
      icon: false,
      bookid: this.$route.query.bookid,
      opcon: [],
      title: [],
      getconttop: null,
      getcont: null,
      id: 0,
      loading: false,
      finished: false,
      currentPage: 1,
    };
  },
  methods: {
    xxx() {
      window.localStorage.setItem("modes", JSON.stringify(this.value));
    },
    onChange() {
      this.xxx();
    },
    scroll(event) {
      if (event.target.offsetHeight + event.target.scrollTop + 10 >= event.target.scrollHeight) {
        console.log("触底");
        this.OpSize();
      }
    },
    showPopup() {
      this.show = true;
    },
    OpSize() {
      this.id += 1;
      this.getconttop(this.id);
    },
    onLoad() {
      this.OpSize();
      this.loading = false;
    },
    PreviousChapter() {
      this.id--;
      this.id = this.id >= 1 ? this.id : 1;
      this.getcont(this.id);
      this.$refs.read.scrollTop = 0;
    },
    NextChapter() {
      this.id++;
      this.getcont(this.id);
      this.$refs.read.scrollTop = 0;
    },
  },

  created() {
    this.id = Number(this.$route.query.id) + 1;
    this.getcont = (id) => {
      getChapterstwo(id)
        .then((res) => {
          this.read = res.data;
          this.title = [res.data.title];
          this.opcon = [
            res.data.cpContent.match(/<p>(.+?)<\/p>/gim).map((item) => item.replace(/(<p>|<\/p>)/gim, "")),
          ];
        })
        .finally(() => {
          window.localStorage.setItem("booklookid", JSON.stringify([this.id, this.$route.query.bookid]));
        });
    };
    this.getconttop = (id) => {
      getChapterstwo(id).then((res) => {
        this.read = res.data;
        this.title = [...this.title, res.data.title];
        this.opcon = [
          ...this.opcon,
          res.data.cpContent.match(/<p>(.+?)<\/p>/gim).map((item) => item.replace(/(<p>|<\/p>)/gim, "")),
        ];
      });
    };
    this.getcont(this.id);
  },
  mounted() {
    // this.OpSize();
  },
  watch: {},
};
</script>

<style lang="less" scoped>
.reading {
  width: 100vw;
  height: 100vh;
  overflow: scroll;
  .content {
    position: relative;
    padding-top: 25px;
    &.dark {
      background: rgb(255, 255, 255);
      color: rgb(0, 0, 0);
    }
    &.dark {
      background: #333;
      color: #eee;
    }
    &.huyan {
      background: #deeeda;
      color: #333;
    }
    &.niupi {
      background: #dfd1c0;
      color: #333;
    }
    .cnt {
      margin-bottom: 30px;
      .title {
        padding: 0 20px;
        font-size: 18px;
        font-weight: 600;
      }
      .con {
        padding: 15px;
        .cons {
          font-size: 14px;
          text-indent: 2em;
          text-align: justify;
          line-height: 1.8;
        }
      }
    }
  }
  .mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    .titles {
      width: 100vw;
      height: 8vh;
      padding: 0 20px;
      font-size: 14px;
      position: fixed;
      top: 0;
      left: 0;
      background-color: rgba(50, 51, 52, 0.9);
      color: white;
      display: flex;
      justify-content: space-between;
      align-items: center;
      // p {
      // }
    }
    .msk {
      width: 100vw;
      height: 67vh;
      position: fixed;
      top: 8vh;
      left: 0;
      ul {
        width: 90px;
        height: 90px;
        border-radius: 5px;
        position: fixed;
        top: 10vh;
        right: 3vw;
        padding-top: 5px;
        background-color: #323334;
        color: white;
        z-index: 9;
        &::after {
          content: "";
          position: absolute;
          top: -13px;
          right: 5vw;
          width: 0;
          height: 0;
          border: 7px solid transparent;
          border-bottom: 7px solid #323334;
        }
        li {
          font-size: 14px;
          text-align: center;
          line-height: 1.8;
          &:nth-child(2) {
            border-top: 1px solid #222;
            border-bottom: 1px solid #222;
          }
        }
      }
    }
    .bottom {
      width: 100vw;
      height: 25vh;
      position: fixed;
      bottom: 0;
      left: 0;
      color: white;
      background-color: rgba(50, 51, 52, 0.9);
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      .top {
        width: 100%;
        height: 4vh;
        display: flex;
        align-items: center;
        .left {
          font-size: 24px;
          margin-right: 5vw;
        }
        .right {
          font-size: 24px;
          margin-left: 5vw;
        }
        .van-slider {
          flex: 1;
        }
      }
      .con {
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        div {
          width: 12vw;
          height: 12vw;
          text-align: center;
          position: relative;
          border-radius: 50%;
          &:first-child,
          &:nth-child(3),
          &:nth-child(4) {
            color: #000;
          }
          p {
            position: absolute;
            left: 2vw;
            bottom: 1vw;
            font-size: 14px;
          }
        }
      }
      .bom {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-around;
        // .left {
        // }
        // .conmuk {
        // }
        // .right {
        // }
      }
    }
  }
}
</style>
