<template>
  <div class="recommend" v-if="books">
    <BookTitle>榜单详情</BookTitle>
    <ul class="content">
      <BookItem v-for="book in books" :key="book._id" :book="book" />
    </ul>
  </div>
</template>

<script>
import { getRank } from "@/apis/index.js";
import BookTitle from "@/components/BookTitle.vue";
import BookItem from "@/components/BookItem.vue";
export default {
  components: {
    BookTitle,
    BookItem,
  },
  data() {
    return {
      books: null,
    };
  },
  created() {
    getRank(this.$route.query.id).then((res) => (this.books = res.data.ranking.books));
  },
};
</script>

<style lang="less" scoped></style>
