<template>
  <div class="loading">
    <van-loading v-if="show" color="#b93321">加载中...</van-loading>
    <p v-else>没有更多了...</p>
  </div>
</template>
<script>
export default {
  data() {
    return {
      show: true,
    };
  },
  created() {
    setTimeout(() => {
      this.show = false;
    }, 3000);
  },
};
</script>

<style lang="less" scoped>
.loading {
  text-align: center;
  color: #b93321;
}
</style>
