<template>
  <div class="homeoptions">
    <van-grid icon-size="40">
      <van-grid-item
        @click="$router.push('/mybooks')"
        icon="data:image/png;base64,"
        text="书架"
      />
      <van-grid-item
        @click="$router.push('/ranking')"
        icon="data:image/png;base64,"
        text="排行"
      />
      <van-grid-item
        @click="myinFace"
        icon="data:image/png;base64,"
        text="VIP"
      />
      <van-grid-item
        @click="myinFacest"
        icon="data:image/png;base64,"
        text="充值"
      />
    </van-grid>
    <div class="record" v-if="booklookid[0]">
      <a class="history" href="#">最近阅读 <span>|</span> </a>
      <p @click.stop="$router.push(`/reading?id=${booklookid[0] - 1}&bookid=${booklookid[1]}`)" class="read">
        {{ title }} 第{{ booklookid[0] }}章
      </p>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { getDetail } from "@/apis/index";
export default {
  data() {
    return {
      booklookid: JSON.parse(window.localStorage.getItem("booklookid")) || [],
      title: "",
    };
  },
  computed: {
    ...mapState(["loginstatus"]),
  },
  methods: {
    myinFace() {
      if (!this.loginstatus) {
        this.$router.push("/source");
      } else {
        this.$router.push("/Myinterface");
      }
    },
    myinFacest() {
      if (!this.loginstatus) {
        this.$router.push("/source");
      } else {
        this.$router.push(`/setting`);
      }
    },
  },
  created() {
    getDetail(this.booklookid[1]).then((res) => (this.title = res.data.title));
  },
};
</script>

<style lang="less" scoped>
.homeoptions {
  border-bottom: 10px solid #f4f4f4;
  .record {
    padding: 0 21px;
    display: flex;
    align-items: center;
    font-size: 12px;
    border-top: 1px solid #ebebeb;
    .history {
      color: #999;
      padding-right: 15px;
      line-height: 3;
      span {
        margin-left: 8px;
      }
    }
    .read {
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      font-size: 12px;
      color: #da6161;
    }
  }
}
</style>
